body,
html {
  background-color: white;
  margin: 0;
  height: 100%;
  width: 100%;
  font-size: 16px;
  font-family: helvetica, arial, sans-serif;
  overflow: hidden;
}
.info {
  position: absolute;
  right: 0;
  top: 0;
  padding: 10px;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.9);
  background: rgba(0, 0, 0, 0.8);
}
.info .keys {
  margin-top: 5px;
  font-weight: 100;
}
